// Process/index.tsx
import React from 'react';
import { StepsContainer, Step, StepNumber, StepTitle, ProcessTitle, ProcessSubtitle } from './styles';
import { ProcessProps } from "./types";


const stepsData = [
  { number: '1', title: 'Discovery Call' },
  { number: '2', title: 'Initial Offer' },
  { number: '3', title: 'Client Interview' },
  { number: '4', title: 'Ballpark Offer' },
  { number: '5', title: 'Acceptance & Contract' },
  { number: '6', title: 'Kick-off Meeting' }
];

const Process = ({id, backgroundColor}: ProcessProps) => {
  return (
    <div id={id}>
    <ProcessTitle>Our Process</ProcessTitle>
    <ProcessSubtitle>What is going to happen after you contact us.</ProcessSubtitle>
    <StepsContainer  style={{backgroundColor: backgroundColor}}>
      {stepsData.map((step, index) => (
        <Step key={index}>
          <StepNumber>{step.number}</StepNumber>
          <StepTitle>{step.title}</StepTitle>
        </Step>
      ))}
    </StepsContainer>
    </div>
  );
};

export default Process;